const LoginPage = () => {
  const [username, setUsername] = useState("");
  const [phoneNum, setPhoneNum] = useState("");
  const [loadingStat, setLoadingStat] = useState(false);

  return (
    <div className="home-content">
      <Navigation state="new" link="/hsok-admin/" title="로그인" />
      <div className="explanation--login">로그인하셔야 합니다.</div>
      <form
        className="login-form"
        name="login-form"
        onSubmit={(e) => {
          e.preventDefault();
          // Auth Handler
          handleLogin(username, phoneNum);
        }}
      >
        <InputContainer order={0} onChange={setUsername} />
        <InputContainer order={1} onChange={setPhoneNum} />
        <div className="other-controls">
          <Link to="/hsok-admin/signup" className="signup-link">
            회원 가입
          </Link>
        </div>
        <input
          type="submit"
          className="button button--diff button--new"
          onChange={(e) => {
            e.preventDefault();
            setLoadingStat(true);
          }}
          value={loadingStat ? "로그인 중..." : "로그인하기"}
        />
      </form>
    </div>
  );
};

export default LoginPage;
